.text_field_form {
  display: flex;
  flex-direction: column;
  width: 318px;
  height: 42px;
  border: none;
}

.text_field_input {
  position: relative;
  width: 305px;
  height: 44px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: #FFFFFF;
  margin-top: 0px;
  margin-bottom: 0px;
}

.text_field_form input[type=text] {
  color: rgba(31, 32, 65, 0.25);
  font-family: "MontserratRegular";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  padding-left: 15px;
}

.text_field_input:focus,
.text_field_input:hover {
  outline: none;
  border: 1px solid rgba(31, 32, 65, 0.25);
}