.type-room {
  width: 270px;
  height: 257px;
  display: flex;
  flex-direction: column;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.type-room-picture1 {
  width: 270px;
  height: 152px;
  display: flex;
  flex-direction: column;
  background: url(image1.svg);
}
.type-room-picture2 {
  width: 270px;
  height: 152px;
  display: flex;
  flex-direction: column;
  background: url(image2.svg);
}
.type-room-first-arrow {
  padding-top: 53px;
  width: 270px;
  height: 44px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.type-room-first-btn-arrow {
  background-color: transparent;
  font-family: "MaterialIcons";
  color: #FFFFFF;
  font-size: 24px;
  line-height: 24px;
  border: none;
  width: 44px;
  height: 44px;
  cursor: pointer;
}
.type-room-first-circle {
  width: 42px;
  height: 8px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 210px;
  padding-top: 31px;
}
.type-room-first-group-circle {
  width: 8px;
  height: 8px;
  background: #FFFFFF;
  border-radius: 4px;
  box-shadow: 0px 5px 5px rgba(31, 32, 65, 0.3);
  border: none;
  cursor: pointer;
}
.type-room-first-group-circle2 {
  width: 6px;
  height: 6px;
  background: transparent;
  border-radius: 4px;
  box-shadow: 0px 5px 5px rgba(31, 32, 65, 0.3);
  border: 1px solid #FFFFFF;
  cursor: pointer;
}
.type-room-first-group-circle2-color {
  align-content: center;
  width: 6px;
  height: 6px;
  border-radius: 3px;
  background: transparent;
  border: none;
}
.type-room-second {
  width: 232px;
  display: flex;
  flex-direction: row;
  justify-content: start;
  padding-top: 20px;
  height: 20px;
  padding-left: 20px;
  margin-bottom: 1px;
}
.type-room-second-number {
  width: 60px;
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 17px;
  color: #1F2041;
}
.type-room-second-number span {
  font-family: "MontserratBold";
  font-size: 19px;
}
.type-room-second-type {
  width: 41px;
  height: 15px;
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #BC9CFF;
  border: none;
  padding-top: 5px;
  padding-right: 20px;
  padding-left: 0px;
}
.type-room-second-type:focus {
  outline: none;
  caret-color: transparent;
}
.type-room-second-cost {
  padding-top: 3px;
  height: 20px;
  font-family: "MontserratBold";
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.5);
}
.type-room-second-cost span {
  padding-left: 5px;
  font-family: "MontserratRegular";
  font-weight: normal;
}
.type-room-line {
  width: 230px;
  height: 1px;
  background: rgba(31, 32, 65, 0.1);
  margin-top: 10px;
  margin-left: 20px;
}
.type-room-fird {
  display: flex;
  flex-direction: row;
  padding-top: 8px;
  padding-left: 20px;
}
.type-room-fird-rates {
  font-family: "MontserratBold";
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.5);
  padding-left: 20px;
}
.type-room-fird-rates span {
  padding-left: 5px;
  font-family: "MontserratRegular";
  font-weight: normal;
}