.seach-room {
  width: 378px;
  height: 372px;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.seach-room-item {
  display: flex;
  flex-direction: column;
  margin: 40px 30px 30px 30px;
}
.seach-room-head {
  font-family: "MontserratBold";
  font-size: 24px;
  line-height: 29px;
  color: #1F2041;
  padding-bottom: 20px;
}
.seach-room-group {
  display: flex;
  flex-direction: row;
  width: 320px;
  height: 64px;
  padding-bottom: 20px;
}
.seach-room-group-row {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
}
.seach-room-date-group-head {
  font-family: "MontserratBold";
  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  color: #1F2041;
  padding-bottom: 5px;
}
.seach-room-guest-head {
  font-family: "MontserratBold";
  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  color: #1F2041;
  padding-bottom: 5px;
}
.seach-room-buttonlong {
  margin-top: -125px;
}