.signin {
  width: 378px;
  height: 356px;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  font-family: "MontserratRegular";
}
.signin-item {
  display: flex;
  flex-direction: column;
  margin-top: 40px;
  margin-left: 30px;
  margin-right: 30px;
}
.signin-head {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 29px;
  color: #1F2041;
  padding-bottom: 20px;
}
.signin-password {
  padding-top: 10px;
}
.signin-buttonlong {
  padding-top: 20px;
}
.signin-lastgroup {
  padding-top: 30px;
  display: flex;
  flex-direction: row;
}
.signin-toxin {
  padding-top: 14px;
  padding-right: 35px;
  width: 188px;
  height: 18px;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}