.rate {
  display: inline-flex;
  width: 120px;
  height: 24px;
}

.material-icons {
  font-family: "MaterialIcons";
  font-size: 24px;
  line-height: 20px;
  background-clip: text;
  background-image: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  color: transparent;
  font-style: normal;
  cursor: pointer;
}