.date-dropdown {
  width: 320px;
  height: 44px;
  position: relative;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 30px;
}
.date-dropdown-block {
  border: none;
  width: 150px;
  height: 44px;
}
.date-dropdown-input {
  position: absolute;
  border: 1px solid rgba(31, 32, 65, 0.25);
  margin-bottom: 0px;
  width: 148px;
  height: 42px;
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.75);
  border-radius: 4px;
  background: #FFFFFF;
  text-align: start;
  cursor: pointer;
}
.date-dropdown-input:focus {
  color: none;
  color: red;
}
.date-dropdown-btn {
  position: relative;
  width: 40px;
  height: 40px;
  background: #FFFFFF;
  color: rgba(31, 32, 65, 0.75);
  border: none;
  box-sizing: border-box;
  padding: 2px;
  margin: 2px;
  cursor: pointer;
  font-family: "MaterialIcons";
  font-size: 24px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.5);
  left: 110px;
  top: 0px;
  background: transparent;
}

.date-dropdown input[type=text] {
  color: rgba(31, 32, 65, 0.75);
  font-family: "MontserratRegular";
  font-style: normal;
  font-weight: normal;
  width: 133px;
  height: 42px;
  font-size: 14px;
  line-height: 18px;
  padding-left: 15px;
}