.room-number {
  width: 378px;
  height: 510px;
  background: #FFFFFF;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.room-number-item {
  display: flex;
  flex-direction: column;
  margin: 40px 30px 30px 30px;
}
.room-number-head {
  display: flex;
  flex-direction: row;
  height: 30px;
  padding-bottom: 20px;
}
.room-number-head-number {
  width: 70px;
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 17px;
  color: #1F2041;
}
.room-number-head-number span {
  font-family: "MontserratBold";
  font-size: 24px;
}
.room-number-head-type {
  padding-top: 5px;
  width: 41px;
  height: 15px;
  border: none;
  padding-left: 6px;
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #BC9CFF;
}
.room-number-head-type:focus {
  outline: none;
  caret-color: transparent;
}
.room-number-head-cost {
  height: 20px;
  padding-left: 90px;
  font-family: "MontserratBold";
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.5);
}
.room-number-head-cost span {
  font-family: "MontserratRegular";
  padding-left: 5px;
}
.room-number-date {
  display: flex;
  flex-direction: column;
  width: 320px;
  height: 64px;
  padding-bottom: 20px;
  font-family: "MontserratRegular";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;
}
.room-number-date-row {
  display: flex;
  flex-direction: row;
}
.room-number-date-row-head {
  padding-right: 87px;
  padding-bottom: 5px;
}
.room-number-guest {
  padding-bottom: 20px;
}
.room-number-guest-head {
  font-family: "MontserratBold";
  text-transform: uppercase;
  font-size: 12px;
  line-height: 15px;
  color: #1F2041;
  padding-bottom: 5px;
}
.room-number-table {
  margin-top: -160px;
  font-family: "MontserratRegular";
  font-size: 14px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.75);
  padding-bottom: 30px;
}
.room-number-table-line {
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  width: 320px;
  height: 24px;
  padding-bottom: 10px;
}
.room-number-table-line-box {
  width: 214px;
  height: 24px;
  padding-right: 6px;
}
.room-number-table-line-right {
  width: 60px;
  text-align: right;
  padding: 0px;
}
.room-number-table-circle {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 1px solid rgba(31, 32, 65, 0.25);
  padding: 0px;
  text-align: center;
}
.room-number-total {
  display: inline-flex;
  padding-bottom: 20px;
  justify-content: space-between;
}
.room-number-total-text {
  font-family: "MontserratBold";
  font-size: 19px;
  line-height: 23px;
  color: #1F2041;
}
.room-number-total-rect {
  width: 176px;
  height: 1px;
  border: 1px dashed rgba(31, 32, 65, 0.25);
  border-top: none;
  border-left: none;
  border-right: none;
  padding-top: 16px;
}