.toggle {
  display: flex;
  flex-direction: row;
}

.check-toggle {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
  margin-right: 10px;
}

/* Hide the browser's default checkbox */
.checkbox-toggle {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

/* Create a custom checkbox */
.checkmark-toggle {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 1px solid rgba(31, 32, 65, 0.25);
  background-color: #FFFFFF;
  border-radius: 10px;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.checkmark-toggle:before {
  position: absolute;
  content: "";
  left: 3px;
  top: 3px;
  background-color: #FFFFFF;
  border: none;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(31, 32, 65, 0.25);
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

/* When the checkbox is checked, add background */
.check-toggle input:checked ~ .checkmark-toggle:before {
  background-color: #FFFFFF;
  border: none;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}

.check-toggle input:checked ~ .checkmark-toggle {
  background-color: #FFFFFF;
  border: 1px solid #bc9cff;
}

/* Create the checkmark/indicator (hidden when not checked) */
.checkmark-toggle:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
.check-toggle input:checked ~ .checkmark-toggle:after {
  display: block;
}

.toggle-text {
  font-family: "MontserratRegular";
  color: rgba(31, 32, 65, 0.5);
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 18px;
  margin-right: 20px;
}