.button {
  display: block;
  border: solid 2px transparent;
  border-radius: 22px;
  padding: 0.1667rem;
  background-image: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 1px #FFFFFF inset;
  cursor: pointer;
  text-decoration-line: none;
  width: 99px;
  height: 44px;
}
.button:hover, .button.button_hovered {
  background: linear-gradient(180deg, rgba(188, 156, 255, 0.5) 0%, rgba(139, 164, 249, 0.5) 100%);
}
.button__wrapper {
  display: flex;
  padding: 0px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border-radius: 22px;
  height: 15px;
}
.button_default {
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  box-shadow: none;
  border: none;
}
.button_bordercolor {
  height: 44px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
}
.button_bordercolor .button__wrapper {
  background-color: white;
}
.button_bordercolor .button__text {
  color: #BC9CFF;
}
.button_link {
  padding: 0;
  width: auto;
  padding: 0;
}
.button.button_link {
  background: white;
}
.button_link .button__wrapper {
  padding: 0;
}
.button_link .button__text {
  color: #BC9CFF;
  line-height: 15px;
}
.button.button_link .button__wrapper .button__text:hover, .button.button_link.button_hovered .button__wrapper .button__text {
  color: rgba(31, 32, 65, 0.5);
}
.button_long.button {
  width: 320px;
  height: 44px;
  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
  box-shadow: none;
  border: none;
}
.button_long .button__wrapper {
  padding: 0;
  width: 100%;
}
.button_long .button__text {
  width: 280px;
  height: 15px;
  margin-left: 20px;
  margin-top: -30px;
}
.button__text {
  font-family: "MontserratRegular";
  text-transform: uppercase;
  font-weight: bold;
  color: white;
  font-size: 12px;
  line-height: 3.3333rem;
  white-space: nowrap;
  height: 44px;
  margin-top: 0px;
  margin-bottom: 10px;
}
.button__arrow {
  font-family: "MaterialIcons";
  color: white;
  font-size: 24px;
  margin-left: auto;
  padding-right: 10px;
}